<template>
  <div v-for='(player, index) in players' :key='player.name'>
    <div class='playerWrapper'>
      <div class='playerName' :style="{color: player.selectedColor}"> {{ player.name }} </div>
      <input class='addScoreInput' type='text' v-model='player.scoreToAdd' inputmode="numeric"
       @keydown.enter='addScore(index)'>
      <button class='addScoreBtn' @click='addScore(index)'>+</button>
      <div class='playerScore'>{{ player.score }}</div>
    </div>
    <div class='needed' v-if='ballsDeep && player.score != winningScore'>
      {{ pointsNeeded[index] }} needed to win
    </div>
  </div>
</template>

<script>

export default {
  name: 'PlayersContainer',

  props: ['players', 'ballsDeep', 'winningScore'],

  emits: ['scoreAdded'],

  computed: {
    pointsNeeded() {
      return this.players.map(player => {
        if (player.score == this.winningScore) { return 0 }
        else { return this.winningScore - player.score + 50 }
      })
    }
  },

  methods: {
    addScore(index) {
      this.$emit('scoreAdded', index)
    },
  }
}

</script>

<style scoped>

.playerWrapper {
  width: 95%;
  margin-top:0.5rem;
  margin-inline: auto;
  display: flex;
  justify-content: center;
  gap:0.25rem;
  color:white;
}
.playerName{
  text-align:right;
  flex-basis:50%;
  overflow:hidden;
}
.addScoreInput {
  width:100%;
  flex-basis:20%;
}
.addScoreBtn{
  flex-basis:10%;
  background-color: #34495E;
}
.playerScore{
  flex-basis:20%;
}
.needed{
  text-align:center;
  color:crimson;
}

</style>
