<template>
  <div class='addPlayerWrapper'>
    <input type='text' placeholder='Quandale' v-model='player' @keydown.enter='addPlayer'>
    <button @click='addPlayer'>+</button>
  </div>
  <div class='pickColor'>
    <div class='flexer'  @click='toggleColors'>
      <div class='colorDefault'>{{ selectedColor }}</div>
      <img src='../assets/logo.png'>
    </div>
    <ul v-if='this.colorsDropdown' class='colorChoices'>
      <li v-for='color in colors' :key='color' :style="{ color: color}"  @click='changeColor(color)'>
      {{ color }} </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'PlayerAdd',

  data() {
    return {
      player: '',
      colors: ['red', 'blue', 'purple', 'green', 'pink', 'orange', 'yellow', 'brown'],
      colorsDropdown: false,
      selectedColor: 'White'
    }
  },

  methods: {
    addPlayer() {
      const newPlayer = { name: this.player, score: 0, selectedColor:this.selectedColor}
      this.$emit('btnClicked', newPlayer)
      this.player = ''
      this.selectedColor = 'White'
    },
    toggleColors(){
      this.colorsDropdown = !this.colorsDropdown
    },
    changeColor(color){
      this.selectedColor = color
      this.colorsDropdown = false
    }
  }
}

</script>

<style scoped>
.addPlayerWrapper{
  display:flex;
  width:69%;
  margin-inline:auto;
  justify-content: center;
  gap:0.5rem;
}
input{
  width:10rem;
}
button{
  width:2rem;
  background-color:#41B883;
}

.colorDefault{
  flex-basis:100%;
  padding-left:0.25rem;
}
.pickColor{
  width:50%;
  position:relative;
  margin-top:0.5rem;
  margin-inline:auto;
}
.colorChoices{
  background-color:rgba(0, 0, 0, 0.699);
  width:100%;
  position:absolute;
  left:0;
  list-style:none;
}
.flexer{
  background-color:white;
  display:flex;
  align-items:center;
}
img{
  margin:0;
  width:1.5rem;
  padding-right:0.25rem;
}
</style>
