<template>
  <BallsDeep v-if='showModal' :showModal='showModal' @closeModal='closeModal'  />
  <div id='logo'> <img src="@/assets/10kvue2.png"> </div>
  <playerAdd @btnClicked='addPlayer' />
  <PlayersContainer @scoreAdded='addScore' :players='players' :ballsDeep='ballsDeep' :winningScore='winningScore' />
</template>

<script>
import PlayerAdd from '@/components/PlayerAdd.vue'
import PlayersContainer from '@/components/PlayersContainer.vue'
import BallsDeep from '@/components/BallsDeep.vue'

export default {
  name: 'App',

  components: {
    PlayerAdd, PlayersContainer, BallsDeep
  },

  data() {
    return {
      players: [],
      ballsDeep: false,
      winningScore: 9999,
      showModal: false
    }
  },

  methods: {
    addPlayer(playerToAdd) {
      this.players.push({ ...playerToAdd, scoreToAdd: null })
    },
    addScore(index) {
      this.players[index].score += Number(this.players[index].scoreToAdd)
      if (this.players[index].score > this.winningScore) {
        if (!this.ballsDeep) { this.showModal = true }
        this.ballsDeep = true
        this.winningScore = this.players[index].score
      }
      this.players[index].scoreToAdd = null
    },
    closeModal() {
      this.showModal = false
    }
  },
}

</script>

